
html, body{
    margin: 0;
    background: #03A9F4;
}
.hello{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #fff;
}
h1{
    font-weight: 100;
    font-size: 50px;
    text-align: center;
}
p{
    font-weight: 100;
    text-align: center;
    letter-spacing: 1px;
}
a{
    text-decoration: none;
    color: inherit;
    border-bottom: 1px dotted #fff;
}
a:hover, a:focus{
    text-decoration: none;
    color: inherit;
    border-bottom: 1px solid #fff;
    outline: none;
}



